import React, {Component} from "react";
import {Button, Card, Table, Space, Modal, Form, Input, message, Select, Badge, Radio, Row, Col} from "antd";
import axios from "axios";
import {globalConfig} from "../../common";
import {EditOutlined, DeleteOutlined, PlusOutlined} from '@ant-design/icons';


export class InterfaceRadio extends Component {
    constructor(props) {
        super(props);
        this.state = {
            option_list: []
        }
    }

    componentDidMount() {
        axios.get(
            globalConfig.domain + '/api/serverInfo/interface/getInterfaceList'
        ).then(
            ret => {
                this.setState({option_list: ret.data.data})
            }
        )
    }

    render() {
        return (
            <Select
                placeholder="Select a interface"
                onChange={this.props.onChange}
                allowClear
            >
                {
                    this.state.option_list.map(
                        (item, index) => {
                            if (item.state.toLowerCase() === 'up'){
                                return <Select.Option key={'item_' + index} value={
                                    this.props.type === 'index' ? item.index : item.ifname
                                }>{item.ifname}</Select.Option>
                            }else{
                                return null;
                            }
                        }
                    )
                }
            </Select>
        )
    }
}

class EditInterfaceAddress extends Component {
    constructor(props) {
        super(props);
        this.formRef = React.createRef();
        this.state = {
            inputDisable: false
        }
    }

    onSubmit = () => {
        this.formRef.current.submit()
    }

    onFinish = (values) => {
        if (this.props.ifname === undefined) {
            return false;
        }
        let url = globalConfig.domain + '/api/config/ipRoute/setIpAddress'

        axios.post(
            url, {
                ifname: this.props.ifname,
                ip: values.ip,
                netmask: values.netmask,
                mode: values.mode,
                state: values.state,
            }
        ).then(
            () => {
                message.success('success');
                this.props.done();
            }
        ).catch(
            e => {
                // console.log(e)
                message.error(e.response.data.detail)
            }
        )
    }

    handleModeChange = (e) => {
        if (e.target.value === 'none') {
            this.formRef.current.setFieldsValue({
                'ip': '127.0.0.1',
                'netmask': '255.255.255.0'
            })
            this.setState({inputDisable: true})
        }
        if (e.target.value === 'static') {
            this.setState({inputDisable: false})
        }
    }

    render() {
        return (
            <Form
                layout={'vertical'}
                name={'editInterface'}
                onFinish={this.onFinish}
                initialValues={
                    {
                        ip: this.props.address,
                        netmask: this.props.netmask,
                        mode: 'static',
                        state: this.props.state,
                    }
                }
                ref={this.formRef}
            >
                <Form.Item label={'Mode'} name={'mode'}>
                    <Radio.Group onChange={this.handleModeChange}>
                        <Radio value={'static'} defaultChecked={true}>Static</Radio>
                        <Radio value={'none'}>None</Radio>
                    </Radio.Group>
                </Form.Item>

                <Form.Item
                    label={'Address'}
                    name={'ip'}
                    rules={[
                        {
                            required: true,
                            message: 'please input ip address!',
                            // max: 15
                        }
                    ]}
                >
                    <Input disabled={this.state.inputDisable} placeholder={'ip address'}/>
                </Form.Item>

                <Form.Item
                    label={'NetMask'}
                    name={'netmask'}
                    rules={[
                        {
                            required: true,
                            message: 'please input netmask!',
                            // max: 15
                        }
                    ]}
                >
                    <Input disabled={this.state.inputDisable} placeholder={'netmask'}/>
                </Form.Item>

                <Form.Item label={'State'} name={'state'}>
                    <Radio.Group>
                        <Radio value={'up'}>Up</Radio>
                        <Radio value={'down'}>Down</Radio>
                    </Radio.Group>
                </Form.Item>
            </Form>
        )
    }

}

