import React, {Component} from "react";
import {Breadcrumb, Layout, Menu} from "antd";
import {SwapOutlined, BlockOutlined, ApartmentOutlined, SettingOutlined, RadarChartOutlined, ForkOutlined} from "@ant-design/icons";

import {Interfaces, StaticRoute, PolicyRoute} from "./component/iproute";
import {Chains} from "./component/iptables";
import {System} from "./component/system";
import {Vip} from "./component/vip";
import {BWList} from "./component/ipset";
import {ArpTables} from "./component/iproute";

const {Sider, Content} = Layout
const {SubMenu} = Menu

export class Config extends Component {

    constructor(props) {
        super(props);
        this.state = {
            content: <Interfaces/>
        }
    }

    handleMenuClick = (e) => {
        // console.log(e.key)
        // eslint-disable-next-line default-case
        switch (e.key) {
            case 'interfaces':
                this.setState({content: <Interfaces/>})
                break;
            case 'arp':
                this.setState({content: <ArpTables/>})
                break;
            case 's_route':
                this.setState({content: <StaticRoute/>})
                break;
            case 'vip':
                this.setState({content: <Vip/>})
                break;
            case 'p_route':
                this.setState({content: <PolicyRoute/>})
                break;
            case 'snat':
                this.setState({content: <Chains titleName={'Nat'} groupType={e.key}/>})
                break;
            case 'dnat':
                this.setState({content: <Chains titleName={'Port Map'} groupType={e.key}/>})
                break;
            case 'filter':
                this.setState({content: <Chains titleName={'Forward'} groupType={e.key}/>})
                break;
            case 'system':
                this.setState({content: <System/>})
                break;
            case 'blacklist':
                this.setState({content: <BWList title={"Black List"} type={"blacklist"} />})
                break;
            case 'whitelist':
                this.setState({content: <BWList title={"White List"} type={"whitelist"}/>})
                break;
        }
    }

    render() {
        return (
            <React.Fragment>
                <Breadcrumb style={{margin: '16px 0'}}>
                    <Breadcrumb.Item>Home</Breadcrumb.Item>
                    <Breadcrumb.Item>Config</Breadcrumb.Item>
                </Breadcrumb>
                <Layout className="site-layout-background" style={{padding: '24px 0'}}>
                    <Sider className="site-layout-background" width={200}>
                        <Menu
                            mode="inline"
                            defaultSelectedKeys={['interfaces']}
                            // defaultOpenKeys={['sub1']}
                            style={{height: '100%'}}
                            onClick={this.handleMenuClick}
                        >
                            <Menu.Item key="interfaces" icon={<ApartmentOutlined />}>Interfaces</Menu.Item>
                            <Menu.Item key="arp" icon={<ForkOutlined />}>Arp</Menu.Item>
                            <Menu.Item key="vip" icon={<RadarChartOutlined />}>VIP</Menu.Item>

                            <SubMenu key="route" icon={<BlockOutlined />} title="Route">
                                <Menu.Item key="s_route">Static Route</Menu.Item>
                                <Menu.Item key="p_route">Policy Route</Menu.Item>
                            </SubMenu>

                            <SubMenu key="policy" icon={<SwapOutlined />} title="Policy">
                                <Menu.Item key="snat">Nat</Menu.Item>
                                <Menu.Item key="dnat">Port map</Menu.Item>
                                <Menu.Item key="filter">Forward</Menu.Item>
                            </SubMenu>
                            <SubMenu key="system_submenu" icon={<SettingOutlined />} title={'System'}>
                                <Menu.Item key="system">System</Menu.Item>
                                <Menu.Item key="blacklist">Black List</Menu.Item>
                                <Menu.Item key="whitelist">White List</Menu.Item>
                                <Menu.Item key="user">User</Menu.Item>
                            </SubMenu>
                        </Menu>
                    </Sider>
                    <Content style={{padding: '0 8px', minHeight: 280}}>{this.state.content}</Content>
                </Layout>
            </React.Fragment>
        )
    }
}
