import {Component} from "react";
import {Card, message, Checkbox} from "antd";
import axios from "axios";
import {globalConfig} from "../../common";


export class System extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: []
        }
    }

    componentDidMount() {
        this.getDataFromServer();
    }

    getDataFromServer = () => {
        axios.get(
            globalConfig.domain + '/api/config/sys/sysConfig'
        ).then(r => this.setState({data: r.data.data}))
    }


    handleSwitchChange = (k, e) => {
        // console.log(k,e.target.checked)
        axios.post(
            globalConfig.domain + '/api/config/sys/sysConfig', {
                item: k,
                value: e.target.checked
            }
        ).then(
            ()=>this.getDataFromServer()
        ).catch(
            e=>message.error(e.response.data.detail)
        )
    }

    render() {
        return (
            <Card title={'System setting'}>
                {
                    this.state.data.map((item, index)=>{
                        return <p key={index}>
                            <Checkbox
                                checked={item.value}
                                onChange={(e) => this.handleSwitchChange(item.item, e)}
                            >
                                {item.desc}
                            </Checkbox>
                        </p>
                    })
                }
            </Card>
        )
    }

}