import {Component} from "react";
import axios from "axios";
import {globalConfig} from "../../common";
import {Card} from "antd";

export class RunningConfig extends Component{
    constructor(props) {
        super(props);
        this.state={
            data:[]
        }
    }

    getRunningConfig=()=>{
        axios.get(
            globalConfig.domain+'/api/config/sys/getRunningConfig'
        ).then(
            r=>this.setState({data: r.data.data})
        )
    }

    componentDidMount() {
        this.getRunningConfig();
    }

    render() {
        return (
            <Card title={'Running Config'}>
                <div>
                    {this.state.data.map(item=><p>{item}</p>)}
                </div>
            </Card>
        )
    }
}