const user_info = window.sessionStorage.getItem('user_info');
const api_url = document.getElementById('api_url').getAttribute('value')

export var globalConfig = {
    domain: api_url.indexOf('{{') === -1 ? api_url : 'http://10.10.4.3:8000',
    userData: user_info ? JSON.parse(user_info): null,
}

export function ts2date(ts, t = 'short'){
    // return new Date(ts*1000).toLocaleString('zh-cn').split(' ')[0].replaceAll('/','-')
    if (t === 'long') {
        return new Date(ts*1000).toLocaleString('zh-cn')
    }
    return new Date(ts*1000).toLocaleString('en').split(' ')[1]
}


export function formatBytes(bytes,label=true) {
    if (bytes === '0' || isNaN(bytes) || bytes === undefined || bytes === 0) return '0';
    let k = 1024;
    let sizes = ['B','KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
    let i = Math.floor(Math.log(bytes) / Math.log(k))
    let x = (bytes / Math.pow(k, i)).toFixed(2)
    if (label){
        return x + ' ' + sizes[i];
    }else{
        return x
    }
}

export function second2str(sec){
    let s = parseInt(sec)
    let d = 0, h = 0, m = 0;
    if (s>=86400){
        d = Math.floor(s/86400)
        s = s%86400
    }

    if (s>=3600){
        h = Math.floor(s/3600)
        s = s%3600
    }

    if (s>=60){
        m = Math.floor(s/60)
        s = s%60
    }

    return `${d} days ${h} hours ${m} minute ${s} seconds`
}


export function logout(){
    window.sessionStorage.setItem('user_info', null);
    window.location.reload();
}