import {Component} from "react";
import {Card, message, Table} from "antd";
import {globalConfig} from "../../common";
import axios from "axios";


export class Sessions extends Component {
    constructor(props) {
        super(props);
        this.state = {
            dataSource: []
        }
    }

    componentDidMount() {
        this.getSessionData();
    }

    getSessionData = () => {
        axios.get(
            globalConfig.domain + '/api/serverInfo/getSysConnSessions'
        ).then(
            r => this.setState({dataSource: r.data.data})
        ).catch(
            e => message.error(e.response.data.detail)
        )
    }

    columns = [
        {title: 'Protocol', dataIndex: 'protocol'},
        {title: 'State', dataIndex: 'tcp_flag'},
        // {title: 'State', dataIndex: 'state', },
        {title: 'TTL', dataIndex: 'ttl'},
        {
            title: 'Request',
            children: [
                {title: 'Source', dataIndex: 'req_src'},
                {title: 'Destination', dataIndex: 'req_dst'},
            ]
        },
        {
            title: 'Response',
            children: [
                {title: 'Source', dataIndex: 'rep_src'},
                {title: 'Destination', dataIndex: 'rep_dst'},
            ]
        },
        {title: 'Mark', dataIndex: 'mark'}
    ]

    render() {
        return (
            <Card title={'Session List'}>
                {/*<div>*/}
                {/*    {this.state.data.map(item=><p>{item}</p>)}*/}
                {/*</div>*/}
                <Table columns={this.columns} dataSource={this.state.dataSource} pagination={false}/>
            </Card>
        )
    }
}
