import React, {Component} from "react";
import {Layout, Menu, Row, Col, Button, Dropdown, message, Modal} from 'antd';
import 'antd/dist/antd.css';
import '../theme.less';
import {ReactComponent as Logo} from '../logo.svg'
import { LogoutOutlined, DownOutlined, SaveOutlined, KeyOutlined } from '@ant-design/icons';
import {Dashboard} from "./dashboard";
import {Config} from "./config";
import {Report} from "./report";
import {globalConfig, logout} from "../common";
import axios from "axios";
import {ChangePassword} from "./component/changePwd";
import {Application} from "./app";

const { Header, Content, Footer } = Layout;


function saveConfig(){
    axios.get(
        globalConfig.domain+'/api/config/sys/saveRunningConfig'
    ).then(
        ()=>message.success('success')

    ).catch(
        ()=>message.error('save cofnig failed!')
    )
}

export class Home extends Component{
    constructor(props) {
        super(props);
        this.Child = React.createRef();
        this.state = {
            content: <Dashboard />,
            visible: false,
            modal_title: null,
            modal_content: null
        }
    }

    menu = (
        <Menu>
            <Menu.Item key={'chang_password'}>
                <Button
                    style={{color:'black'}}
                    type={'link'}
                    icon={<KeyOutlined />}
                    onClick={
                        ()=>this.setState(
                            {
                                visible: true,
                                modal_title: 'Change Password',
                                modal_content: <ChangePassword ref={this.Child} onFinish={this.handleChangePasswordFinish} />
                            }
                        )
                    }
                >Change Password</Button>
            </Menu.Item>

            <Menu.Item key={'save'}>
                <Button style={{color:'black'}} type={'link'} icon={<SaveOutlined />} onClick={()=>saveConfig()}>Save Configuration</Button>
            </Menu.Item>

            <Menu.Item key={'logout'}>
                <Button style={{color:'black'}} type={'link'} icon={<LogoutOutlined/>} onClick={()=>logout()}>Logout</Button>
            </Menu.Item>

            {/*<Menu.Item danger>a danger item</Menu.Item>*/}
        </Menu>
    );

    handleChangePasswordFinish = (values)=>{
        console.log(values)
    }

    pageChange = (e)=>{
        // eslint-disable-next-line default-case
        switch (e.key){
            case 'dashboard':
                this.setState({content: <Dashboard />});
                break;
            case 'config':
                this.setState({content: <Config />});
                break;
            case 'report':
                this.setState({content: <Report />});
                break;
            case 'app':
                this.setState({content: <Application />});
                break;
        }
    }

    render(){
        return (
            <Layout>
                <Header className="header">
                    <Row>
                        <Col span={20}>
                            <Logo stroke="white" height={40} width={40} style={{marginTop:14, marginRight:20, float:'left'}} />
                            <div
                                style={
                                    {
                                        float: "left",
                                        width:0,
                                        height:40,
                                        overflow: "hidden",
                                        margin: '13px 0',
                                        borderRight: '0.5px solid #666',
                                        borderLeft: '0.5px solid #000',
                                    }
                                }
                            > </div>
                            <div
                                style={
                                    {
                                        color:"white",
                                        float:"left",
                                        margin:'0 20px',
                                        // marginLeft: -10,
                                        fontSize: 16,
                                        fontWeight:"bolder"
                                    }
                                }
                            >FuRong Cloud FW</div>
                            <Menu theme="dark" mode="horizontal" defaultSelectedKeys={['dashboard']} onClick={this.pageChange}>
                                <Menu.Item key="dashboard">Dashboard</Menu.Item>
                                <Menu.Item key="config">Config</Menu.Item>
                                <Menu.Item key="app">Apps</Menu.Item>
                                <Menu.Item key="report">Report</Menu.Item>
                            </Menu>
                        </Col>

                        <Col span={4}>
                            <Dropdown overlay={this.menu} trigger={['click']}>
                                {/* eslint-disable-next-line */}
                                <a
                                    href={'#'}
                                    style={{
                                        float:'right',
                                        color: '#fff'
                                    }}
                                    className="ant-dropdown-link"
                                    onClick={e => e.preventDefault()}
                                >
                                    {globalConfig.userData.first_name} <DownOutlined />
                                </a>
                            </Dropdown>
                        </Col>
                    </Row>

                </Header>
                <Content style={{padding:"0 50px",minHeight: document.documentElement.clientHeight - (64+70)}}>
                    {this.state.content}
                    <Modal
                        destroyOnClose={true}
                        tile={'Change Password'}
                        visible={this.state.visible}
                        onCancel={
                            ()=>this.setState({visible: false, modal_title: null, modal_content: null})
                        }
                        onOk={()=>this.Child.current.submit()}
                        title={this.state.modal_title}
                    >
                        {this.state.modal_content}
                    </Modal>
                </Content>
                <Footer style={{ textAlign: 'center' }}>FuRong Cloud Data Center Design 2021 Created by Ant UED</Footer>
            </Layout>
        )
    }
}
