export class Chains extends Component {

    constructor(props) {
        super(props);
        this.Child = React.createRef();
        this.ruleRecord = null;
        this.state = {
            page_tag: 'chain',
            chain_name: null,
            disabled: false,

            dataSource: [],

            ruleData: [],
            groupType: this.props.groupType,
            modal_content: null,
            modal_title: null,
            visible: false,
        }
        // console.log(this.props)
    }

    columns = [
        {
            title: 'Name', dataIndex: 'chain_name', width: 200,
            render: (v, record) => {
                let s = {
                    color: record.manual ? 'gray' : 'black',
                    textDecoration: record.manual ? 'line-through' : "none"
                }
                if (record.is_rule) {
                    s.color = 'red'
                }
                if (record.is_rule) {
                    return <span style={s}>{v}</span>
                }
                // eslint-disable-next-line jsx-a11y/anchor-is-valid
                return <a href={'#'} title={v} style={s} onClick={() => this.getAllRulesByChain(record)}>{v}</a>
            }
        },
        {title: 'In Interface', dataIndex: 'in_interface', render:(v)=>v === null?'*':v},
        {title: 'Source', dataIndex: 'src',},
        {title: 'Destination', dataIndex: 'dst',},
        {
            title: 'Counters', children: [
                {
                    title: 'Packets',
                    dataIndex: 'packets',
                    render: v => v
                }, {
                    title: 'Bytes',
                    dataIndex: 'bytes',
                    render: v => formatBytes(v)
                },

            ]
        },
        // {title: 'Add Date', dataIndex: 'pub_date', render: v => v.substr(0, 16)},
        {
            title: 'Action',
            dataIndex: 'id',
            render: (v, record) => {
                return (
                    <Space>
                        {/*<Button icon={<PlusOutlined/>} disabled={record.manual} onClick={() => this.handleAddRule(record)}>Insert Rule</Button>*/}
                        <Button disabled={record.is_rule} icon={<DeleteOutlined/>}
                                onClick={() => this.handleDeleteChain(record)}>Delete</Button>
                    </Space>
                )
            }
        },
    ];

    onSortEnd = ({oldIndex, newIndex}) => {
        // console.log(oldIndex, newIndex)
        const {dataSource} = this.state;
        const old_rule_seq = dataSource[oldIndex].rule_seq
        const new_rule_seq = dataSource[newIndex].rule_seq
        if (old_rule_seq === -1 || new_rule_seq === -1) {
            return Modal.info({
                title: 'Warning',
                content: 'some rule not enable'
            })
        }
        if (oldIndex !== newIndex) {
            axios.put(
                globalConfig.domain + '/api/config/iptables/changeRule', {
                    chain_name: this.state.chain_name,
                    old_index: old_rule_seq,
                    new_index: new_rule_seq,
                }
            ).then(
                () => {
                    const newData = arrayMoveImmutable([].concat(dataSource), oldIndex, newIndex).filter(el => !!el);
                    console.log('Sorted items: ', newData);
                    this.setState({dataSource: newData});
                }
            ).catch(
                e => Modal.error({
                    title: 'error',
                    content: e.response.data.detail
                })
            )

        }
    };

    DraggableContainer = props => {
        return (
            <SortableContainer
                useDragHandle
                disableAutoscroll
                helperClass="row-dragging"
                onSortEnd={this.onSortEnd}
                {...props}
            />
        )
    }

    DraggableBodyRow = ({className, style, ...restProps}) => {
        const {dataSource} = this.state;
        // console.log(restProps['data-row-key'],'data-row-key')
        // function findIndex base on Table rowKey props and should always be a right array index
        const index = dataSource.findIndex(x => {
            // console.log(x.id)
            return x.id === restProps['data-row-key']
        });
        // console.log(index,'index')
        return <SortableItem index={index} {...restProps} />;
    };

    ruleMoreMenu = (record) => (
        <Menu key={'rule_menu'} onClick={(e) => this.handleRuleAction(e, record)}>
            <Menu.Item
                key={record.enable === 1 ? 'pause' : 'resume'}
            >
                {record.enable === 1 ? 'Pause' : 'Resume'}
            </Menu.Item>

            <Menu.Item key={'delete'}>
                Delete
            </Menu.Item>
        </Menu>
    )

    ruleColumns = [
        {title: 'Sort', dataIndex: 'rule_seq', width: 70, render: () => <DragHandle/>, fixed: 'left'},
        // {title: 'Comment', dataIndex: 'comment', fixed: 'left', width: 150},
        {
            title: 'Source',
            children: [
                {title: 'addr', dataIndex: 'src', width: 180},
                {title: 'port', dataIndex: 'sport', width: 130, render: v => v ? v.replaceAll(' ', '') : null},
            ]
        },
        {
            title: 'Destination',
            width: 250,
            children: [
                {title: 'addr', dataIndex: 'dst', width: 180},
                {title: 'port', dataIndex: 'dport', width: 130, render: v => v ? v.replaceAll(' ', '') : null},
            ]
        },
        {title: 'Protocol', dataIndex: 'protocol', width: 95},
        {title: 'In Interface', dataIndex: 'in_interface', width: 120},
        {
            title: 'Target',
            children: [
                {title: 'Action', dataIndex: 'target', width: 100},
                {title: 'Out Interface', dataIndex: 'out_interface', width: 130},
                {title: 'To Destination', dataIndex: 'to_destination', width: 150},
                {title: 'To source', dataIndex: 'to_source', width: 150},
                {title: 'To ports', dataIndex: 'to_ports', width: 150},
            ]
        },

        {
            title: 'Status',
            children: [
                {
                    title: 'State',
                    dataIndex: 'enable',
                    width: 105,
                    render: (v) => <Badge status={v === 1 ? 'processing' : 'error'}
                                          text={v === 1 ? 'running' : 'disabled'}/>
                },
                {title: 'Packets', dataIndex: 'packets', width: 120},
                {title: 'Bytes', dataIndex: 'bytes', width: 120, render: (v) => formatBytes(v)}
            ]
        },
        {
            title: 'Operate', dataIndex: 'id', fixed: 'right', width: 100, render: (v, record) => {
                return (
                    <Dropdown disabled={this.state.disabled} overlay={this.ruleMoreMenu(record)} trigger={['click']}>
                        {/*eslint-disable-next-line*/}
                        <a>
                            More <DownOutlined/>
                        </a>
                    </Dropdown>
                )
            }
        },
    ]

    componentDidMount() {
        this.getAllChains(this.props);
    }

    handleRuleAction = (e, record) => {
        let chain_name = record.id > 0 ? record.chain.chain_name : record.chain_name
        // eslint-disable-next-line default-case
        switch (e.key.toLowerCase()) {
            case 'pause':
                record.enable = 0;
                this.pauseAndResumeEvent(record);
                break;
            case 'resume':
                record.enable = 1;
                this.pauseAndResumeEvent(record);
                break;
            case 'delete':
                Modal.confirm({
                    title: 'delete rule',
                    content: 'deleting rule from ' + chain_name,
                    okType: "danger",
                    onOk: () => {
                        let data = clearObject(record)
                        data.chain_name = chain_name
                        axios.delete(
                            globalConfig.domain + '/api/config/iptables/setRule/' + this.props.groupType, {
                                data: data
                            }
                        ).then(
                            () => {
                                message.success('success');
                                this.getAllRulesByChain({chain_name: this.state.chain_name})
                            }
                        ).catch(e => message.error(e.response.data.detail))
                    }
                })
                break;
        }
    }

    pauseAndResumeEvent = (record) => {
        let chain_name = record.id > 0 ? record.chain.chain_name : record.chain_name;
        let data = clearObject(record);
        data.chain_name = chain_name;
        axios.put(
            globalConfig.domain + '/api/config/iptables/setRule/' + this.props.groupType, data
        ).then(
            () => {
                message.success('success');
                this.getAllRulesByChain({chain_name: this.state.chain_name})
            }
        ).catch(e => message.error(e.response.data.detail))
    }

    getAllChains = (props) => {
        if (['snat', 'dnat', 'filter'].indexOf(props.groupType) === -1) {
            return false;
        }
        axios.get(
            globalConfig.domain + '/api/serverInfo/iptables/getAllChain', {
                params: {
                    group_type: props.groupType
                }
            }
        ).then(
            ret => {
                this.setState({
                    dataSource: ret.data.data,
                    page_tag: 'chain'
                })
            }
        )
    }

    onOk = () => {
        this.Child.current.onSubmit()
    }

    onCancel = () => {
        this.setState({visible: false, modal_content: null})
    }

    getAllRulesByChain = (record) => {
        let table_name = this.props.groupType === 'filter' ? 'filter' : 'nat'
        axios.get(
            globalConfig.domain + '/api/serverInfo/iptables/getAllRulesByChain', {
                params: {
                    chain_name: record.chain_name,
                    table_name: table_name,
                }
            }
        ).then(r => {
            this.setState({
                dataSource: r.data.data,
                page_tag: 'rule',
                disabled: record.manual,
                chain_name: record.chain_name
            })
        })
    }

    handleDeleteChain = (record) => {
        // let data = {
        //     table_name: record.table_name,
        //     chain_name: record.chain_name,
        //     nat_mode: record.group_type,
        //     src: record.src,
        //     dst: record.dst
        // }

        if (record.table_name === 'nat') {
            record.nat_mode = record.group_type
        }

        for (let i in record){
            if (record[i] === null){
                delete record[i]
            }
        }

        Modal.confirm({
            title: 'deleting policy',
            content: 'delete policy will delete all relate rules',
            okType: 'danger',
            onOk: () => {
                axios.delete(
                    globalConfig.domain + '/api/config/iptables/setChainGroup',
                    {
                        data: record
                    }
                ).then(
                    () => {
                        message.success('success');
                        this.getAllChains(this.props)
                    }
                ).catch(
                    e => message.error(e.response.data.detail)
                )
            }
        })
    }

    subComponentDone = () => {
        this.setState({visible: false});
        if (this.state.page_tag === 'chain') {
            this.getAllChains(this.props);
        } else {
            this.getAllRulesByChain({chain_name: this.state.chain_name});
        }
    }

    handleAddChainGroup = () => {
        this.setState({
            visible: true,
            modal_content: <NewChain done={this.subComponentDone} ref={this.Child} groupType={this.props.groupType}/>,
            modal_title: 'Add ' + this.props.groupType.toUpperCase() + ' Policy'
        })
    }

    handleAddRule = (chain_name) => {
        let c = <NewRule done={this.subComponentDone} ref={this.Child} groupType={this.props.groupType}
                         chainName={chain_name}/>
        this.setState({
            visible: true,
            modal_content: c,
            modal_title: 'Add rule to ' + chain_name + ' policy'
        })
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.props.groupType !== prevProps.groupType) {
            this.getAllChains(this.props)
        }
    }

    render() {
        return (
            <Card title={this.props.titleName + ' Policy'}>
                <Space style={{width: '100%'}} direction={'vertical'} size={'large'}>
                    {
                        this.state.page_tag === 'chain' ? (
                            <Button
                                type={'primary'}
                                icon={<PlusOutlined/>}
                                onClick={this.handleAddChainGroup}
                            >
                                Add Policy
                            </Button>
                        ) : (
                            <Space>
                                <Button
                                    icon={<LeftOutlined/>}
                                    onClick={() => this.getAllChains(this.props)}
                                >
                                    Back
                                </Button>
                                <Button
                                    icon={<PlusOutlined/>}
                                    disabled={this.state.disabled}
                                    onClick={
                                        () => this.handleAddRule(this.state.chain_name)
                                    }
                                >
                                    Insert Rule
                                </Button>
                            </Space>
                        )
                    }

                    <Table
                        pagination={false}
                        dataSource={this.state.dataSource}
                        columns={this.state.page_tag === 'chain' ? this.columns : this.ruleColumns}
                        rowKey={(record) => record.id}
                        scroll={{x: 1300}}
                        components={this.state.page_tag === 'chain' ? {} : {
                            body: {
                                wrapper: this.DraggableContainer,
                                row: this.DraggableBodyRow,
                            },
                        }}
                        // bordered
                    />
                </Space>

                <Modal
                    destroyOnClose={true}
                    title={this.state.modal_title}
                    visible={this.state.visible}
                    onOk={this.onOk}
                    onCancel={this.onCancel}
                >
                    {this.state.modal_content}
                </Modal>
            </Card>
        )
    }
}