import React, {Component} from "react";
import {Breadcrumb, Layout, Menu} from "antd";

import {ReactComponent as OpenVpnOutlined} from '../openvpn.svg'
import {OpenVpnStatus, OpenVpnUsers} from "./component/openvpn";

const {Sider, Content} = Layout
const {SubMenu} = Menu

export class Application extends Component {

    constructor(props) {
        super(props);
        this.state = {
            content: <OpenVpnStatus/>
        }
    }

    handleMenuClick = (e) => {
        // console.log(e.key)
        // eslint-disable-next-line default-case
        switch (e.key) {
            case 'openvpn_status':
                this.setState({content: <OpenVpnStatus/>})
                break;
            case 'openvpn_users':
                this.setState({content: <OpenVpnUsers/>})
                break;
        }
    }

    render() {
        return (
            <React.Fragment>
                <Breadcrumb style={{margin: '16px 0'}}>
                    <Breadcrumb.Item>Home</Breadcrumb.Item>
                    <Breadcrumb.Item>App</Breadcrumb.Item>
                </Breadcrumb>
                <Layout className="site-layout-background" style={{padding: '24px 0'}}>
                    <Sider className="site-layout-background" width={200}>
                        <Menu
                            mode="inline"
                            defaultSelectedKeys={['openvpn_status']}
                            defaultOpenKeys={['openvpn']}
                            style={{height: '100%'}}
                            onClick={this.handleMenuClick}
                        >

                            <SubMenu key="openvpn" icon={<OpenVpnOutlined width={18} fill={'#666'} />} title="OpenVPN">
                                <Menu.Item key="openvpn_status">Status</Menu.Item>
                                <Menu.Item key="openvpn_users">User</Menu.Item>
                            </SubMenu>

                            {/*<SubMenu key="policy" icon={<SwapOutlined />} title="Policy">*/}
                            {/*    <Menu.Item key="snat">Nat</Menu.Item>*/}
                            {/*    <Menu.Item key="dnat">Port map</Menu.Item>*/}
                            {/*    <Menu.Item key="filter">Forward</Menu.Item>*/}
                            {/*</SubMenu>*/}
                        </Menu>
                    </Sider>
                    <Content style={{padding: '0 8px', minHeight: 280}}>{this.state.content}</Content>
                </Layout>
            </React.Fragment>
        )
    }
}
