import React, {Component} from "react";
import {Button, Card, Form, Input, Modal, Space, Table, message, InputNumber} from "antd";
import {PlusOutlined, DeleteOutlined} from "@ant-design/icons";
import axios from "axios";
import {globalConfig} from "../../common";


export class BWList extends Component{
    constructor(props) {
        super(props);
        this.child = React.createRef()
        this.state = {
            dataSource: [],
            visible: false
        }
    }

    get_all_host_from_server(){
        axios.get(
            globalConfig.domain+'/api/config/sys/ipset',{
                params: {
                    type: this.props.type
                }
            }
        ).then(
            r=>this.setState({dataSource: r.data.data})
        )
    }

    handleAddHostToList = ()=>{
        this.setState({visible: true})
    }

    onCancel = ()=>{
        this.setState({visible: false})
    }

    onOk = ()=>{
        this.child.current.submit()
    }

    onFinish = (values)=>{
        axios.post(
            globalConfig.domain+'/api/config/sys/ipset', values,{
                params:{
                    type: this.props.type
                }
            }
        ).then(
            r=>{
                message.success(r.data.msg);
                this.setState({visible: false});
                this.get_all_host_from_server();
            }
        ).catch(
            e=>Modal.error({title: "error!", content: e.response.data.detail})
        )
    }

    componentDidMount() {
        this.get_all_host_from_server();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.props.type !== prevProps.type){
            this.get_all_host_from_server();
        }
    }

    deleteIpSetRecord=(v)=>{
        axios.delete(
            globalConfig.domain+'/api/config/sys/ipset',{
                params:{
                    type:this.props.type
                },
                data:{
                    ip: v.ip
                }
            }
        ).then(
            r=>{
                message.success(r.data.msg);
                this.get_all_host_from_server();
            }

        ).catch(
            e=>message.error(e.response.data.detail)
        )
    }

    columns = [
        {title: 'Id', dataIndex: 'id'},
        {title: 'Ip', dataIndex: 'ip'},
        {title: 'Timeout', dataIndex: 'timeout'},
        {title: 'Action', key: 'action', render:(record)=><Button icon={<DeleteOutlined/>} onClick={()=>this.deleteIpSetRecord(record)}>Delete</Button>
        }
    ]

    render() {
        return (
            <Card title={this.props.title}>
                <Space style={{width: '100%'}} direction={'vertical'} size={'large'}>
                    <Button
                        type={'primary'}
                        icon={<PlusOutlined/>}
                        onClick={this.handleAddHostToList}
                    >
                        Add Host
                    </Button>
                    <Table
                        pagination={false}
                        dataSource={this.state.dataSource}
                        columns={this.columns}
                        rowKey={(record) => record.id}
                    />
                </Space>
                <Modal
                    destroyOnClose={true}
                    title={'Add host to '+this.props.title}
                    visible={this.state.visible}
                    onOk={this.onOk}
                    onCancel={this.onCancel}
                >
                    <Form name={'form'} layout={'vertical'} ref={this.child} onFinish={this.onFinish}>
                        <Form.Item
                            name={'ip'}
                            label={'Ip Address'}
                            rules={
                                [
                                    {
                                        required:true,
                                        message: 'please input a correct ip address',
                                        max: 18,
                                    }
                                ]
                            }
                        >
                            <Input placeholder={'10.10.10.1'} />
                        </Form.Item>

                        <Form.Item
                            name={'timeout'}
                            label={'Timeout'}
                            initialValue={0}
                        >
                            <InputNumber max={86400} min={0} onChange={v=>this.child.current.setFieldsValue({timeout:parseInt(v)})} />
                        </Form.Item>
                    </Form>
                </Modal>
            </Card>
        )
    }
}