import React, {Component} from "react";
import {
    Breadcrumb, Layout, Row, Col, Card
} from "antd";
import {Line, Gauge, Liquid, Pie} from '@ant-design/charts'
import {globalConfig, formatBytes, second2str} from "../common";
import axios from "axios";
// import {LaptopOutlined, NotificationOutlined, UserOutlined} from "@ant-design/icons";

const {Content} = Layout

const styles = {
    field: {
        display: 'inline-block',
        marginRight: 10,
        textAlign: 'right',
        width: 100
    }
};

const liquidConfig = {
    height: 180,
    // percent: 0.25,
    outline: {
        border: 4,
        distance: 8,
    },
    wave: {length: 128},
}

const gaugeConfig = {
    height: 180,
    padding: 4,
    range: {
        ticks: [0, 1],
        color: ['l(0) 0:#F4664A 0.5:#FAAD14 1:#30BF78'],
    },
    indicator: {
        pointer: {style: {stroke: '#D0D0D0'}},
        pin: {style: {stroke: '#D0D0D0'}},
    },
    statistic: {
        content: {
            offsetY: 5,
            style: {
                fontSize: '22px',
                color: '#4B535E',
            },
            // formatter: function formatter() {
            //     return 'CPU util';
            // },
        },
    },
}

const pieConfig = {
    height:220,
    // appendPadding: 10,
    angleField: 'value',
    colorField: 'name',
    radius: 1,
    innerRadius: 0.8,
    label: { type: 'outer' },
    interactions: [{ type: 'element-active' }, { type: 'element-selected' }],
}

const sessioniPieConfig = {
    padding: 10,
    height:220,
    // appendPadding: 10,
    angleField: 'hits',
    colorField: 'host',
    radius: 1,
    // innerRadius: 0.8,
    label: { type: 'outer' },
    // interactions: [{ type: 'element-active' }, { type: 'element-selected' }],
}

const lineConfig = {
    xField: 'date',
    yField: 'value',
    height: 220,
    margin: 10,
    color: ['#1979C9', '#D62A0D', '#FAA219'],
};

export class Dashboard extends Component {

    constructor(props) {
        super(props);
        this.timer = null;
        this.state = {
            loadData: [],
            inData: [],
            outData: [],
            basicInfo: {},
            cpu_util: 0,
            mem_util: 0,
            disk_util: 0,
            tcp_session: [],
            rule_aggr:[],
            chain_aggr:[],
            session_count: []
        }
    }

    componentDidMount() {
        // setInterval(this.ajaxGetData, 5000)
        this.ajaxGetData();
        // this.timer = setInterval(()=>this.ajaxGetData(false), 6000)
        // this.setState({timer: timer})
        // console.log('1234')
    }

    componentWillUnmount() {
        // console.log('4567')
        // clearInterval(this.timer)
    }

    ajaxGetData = (init=true) => {

        let disk_usage = axios.get(
            globalConfig.domain+'/api/serverInfo/disk_usage'
        ).then(
            r=>this.setState({
                disk_util: r.data.data.util
            })
        )

        let session_count = axios.get(
            globalConfig.domain+'/api/serverInfo/hostSessionCount'
        ).then(
            r=>this.setState({
                session_count: r.data.data
            })
        )

        let loadReq = axios.get(
            globalConfig.domain + '/api/serverInfo/sar/load',{
                params:{
                    time_range: init ? '10m' : '5s'
                }
            }
        ).then(
            ret => {
                let data = init ? [] : this.state.loadData;
                ret.data.data.map((item) => {
                    data.unshift({
                        date: item.datetime,
                        category: 'load1',
                        value: item.load1,
                    })
                    data.unshift({
                        date: item.datetime,
                        category: 'load5',
                        value: item.load5,
                    })
                    data.unshift({
                        date: item.datetime,
                        category: 'load15',
                        value: item.load15,
                    })
                    return false;
                })
                this.setState({loadData: data})
                // console.log(data)
            }
        )

        let tcpReq = axios.get(
            globalConfig.domain + '/api/serverInfo/sar/tcp', {
                params:{
                    time_range: init ? '30m' : '5s'
                }
            }
        ).then(
            ret => {
                let data = init ? [] : this.state.tcp_session;
                ret.data.data.map((item) => {
                    data.unshift({
                        date: item.datetime,
                        category: 'tcp_active',
                        value: item.tcp_active/10000,
                    })
                    data.unshift({
                        date: item.datetime,
                        category: 'tcp_passive',
                        value: item.tcp_passive/10000,
                    })
                    return false;
                })
                this.setState({tcp_session: data})
                // console.log(data)
            }
        )

        let netReq = axios.get(
            globalConfig.domain + '/api/serverInfo/sar/net',{
                params:{
                    time_range: init ? '10m' : '5s'
                }
            }
        ).then(
            ret => {
                let inData = init ? [] : this.state.inData;
                let outData = init ? [] : this.state.outData;

                ret.data.data.map((item) => {
                    inData.unshift({
                        interface: item.interface,
                        date: item.datetime,
                        value: parseInt(item.in_bytes /10000)
                    })
                    outData.unshift(
                        {
                            interface: item.interface,
                            date: item.datetime,
                            // value: formatBytes(item.out_bytes, false)
                            value: parseInt(item.out_bytes /10000)
                        }
                    )
                    return false;
                })
                this.setState({
                    inData: inData,
                    outData: outData
                })
            }
        )

        let basicInfoReq = axios.get(
            globalConfig.domain + '/api/serverInfo/basicInfo'
        ).then(
            ret => this.setState({basicInfo: ret.data.data})
        )

        let cpuReq = axios.get(
            globalConfig.domain + '/api/serverInfo/sar/cpu'
        ).then(
            ret => {
                this.setState({
                    cpu_util: ret.data.data[0].cpu_util / 100
                })
            }
        )

        let memReq = axios.get(
            globalConfig.domain + '/api/serverInfo/sar/memory'
        ).then(
            ret => {
                this.setState({
                    mem_util: parseInt(ret.data.data[0].mem_util) / 100
                })
            }
        )

        let chainAggr = axios.get(
            globalConfig.domain+'/api/serverInfo/iptables/aggregation/chainRule'
        ).then(
            r=>this.setState({
                rule_aggr: r.data.data.rule,
                chain_aggr: r.data.data.chain
            })
        )

        // let iptablesEventAggr = axios.get(
        //     globalConfig.domain+'/api/serverInfo/iptables/aggregation/event'
        // ).then(
        //     r=>this.setState({
        //         event_aggr: r.data.data
        //     })
        // )

        Promise.all([loadReq, netReq, basicInfoReq, cpuReq, memReq, tcpReq, chainAggr, disk_usage, session_count]).catch(
            e=>{
                console.log(e)
            }
        )
    }

    render() {
        return (
            <React.Fragment>
                <Breadcrumb style={{margin: '16px 0'}}>
                    <Breadcrumb.Item>Home</Breadcrumb.Item>
                    <Breadcrumb.Item>Dashboard</Breadcrumb.Item>
                </Breadcrumb>
                <Layout className="site-layout-background" style={{padding: '24px 0'}}>
                    <Content>
                        <Row gutter={[8, 8]}>
                            <Col span={6}>
                                <Card title={'Host Information'}>
                                    <p><b style={styles.field}>hostname</b>{this.state.basicInfo.hostname}</p>
                                    <p><b
                                        style={styles.field}>uptime</b>{second2str(this.state.basicInfo.running_total_time)}
                                    </p>
                                    <p><b style={styles.field}>sys
                                        time</b>{new Date(this.state.basicInfo.system_time * 1000).toDateString()}</p>
                                    <p><b style={styles.field}>cpu core</b>{this.state.basicInfo.cpu_count}</p>
                                    <p><b style={styles.field}>memory
                                        total</b>{formatBytes(this.state.basicInfo.mem_total)}</p>
                                </Card>
                            </Col>
                            <Col span={6}>
                                <Card title={'Cpu Utilization'}>
                                    <Gauge {...gaugeConfig} percent={this.state.cpu_util}/>
                                </Card>
                            </Col>
                            <Col span={6}>
                                <Card title={'Memory Utilization'}>
                                    <Liquid {...liquidConfig} percent={this.state.mem_util}/>
                                </Card>
                            </Col>

                            <Col span={6}>
                                <Card title={'Disk Utilization'}>
                                    <Liquid {...liquidConfig} theme={{ styleSheet: { brandColor: '#FAAD14' } }} percent={this.state.disk_util}/>
                                </Card>
                            </Col>

                            <Col span={8}>
                                <Card title={'Rule Count'}>
                                    <Pie {...pieConfig} data={this.state.rule_aggr} />
                                </Card>
                            </Col>
                            <Col span={8}>
                                <Card title={'Policy Count'}>
                                    <Pie {...pieConfig} data={this.state.chain_aggr} />
                                </Card>
                            </Col>

                            <Col span={8}>
                                <Card title={'Session Count'}>
                                    <Pie {...sessioniPieConfig} data={this.state.session_count} />
                                </Card>
                            </Col>

                            <Col span={8}>
                                <Card title={'System Load'}>
                                    <Line
                                        smooth={false}
                                        data={this.state.loadData}
                                        seriesField={'category'}
                                        {...lineConfig} />
                                </Card>
                            </Col>
                            <Col span={8}>
                                <Card title={'In traffic'}>
                                    <Line
                                        data={this.state.inData}
                                        seriesField={'interface'}
                                        smooth={false}
                                        tooltip={{
                                            formatter: (datum) => {
                                                return { name: datum.interface, value: formatBytes(datum.value) };
                                            }
                                        }}
                                        yAxis={
                                            {
                                                label: {
                                                    formatter: (v) => formatBytes(v)
                                                },
                                            }
                                        }
                                        {...lineConfig}
                                    />
                                </Card>
                            </Col>
                            <Col span={8}>
                                <Card title={'Out traffic'}>
                                    <Line
                                        tooltip={{
                                            formatter: (datum) => {
                                                return { name: datum.interface, value: formatBytes(datum.value) };
                                            }
                                        }}
                                        smooth={false}
                                        data={this.state.outData}
                                        seriesField={'interface'}
                                        yAxis={
                                            {
                                                label: {
                                                    formatter: (v) => formatBytes(v)
                                                },
                                            }
                                        }
                                        {...lineConfig} />
                                </Card>
                            </Col>

                            <Col span={24}>
                                <Card title={'Tcp Session'}>
                                    <Line
                                        smooth={false}
                                        data={this.state.tcp_session}
                                        seriesField={'category'}
                                        {...lineConfig} />
                                </Card>
                            </Col>
                        </Row>
                    </Content>
                </Layout>
            </React.Fragment>
        )
    }
}
