import React, {Component} from "react";
import {Button, Form, Input, Row, Col, message} from "antd";
import {UserOutlined, LockOutlined, VerifiedOutlined} from '@ant-design/icons';
import bg from '../assets/bg.jpg'
import ad from '../assets/ad.png'
import {globalConfig} from "../common";
import axios from "axios";

const styles = {
    bg: {
        position: 'absolute',
        width: '100%',
        height: '100%',
        padding: 0,
        margin: 0,
        top: 0,
        bottom: 0,
        background: '#00032c url(' + bg + ') no-repeat center center',
        backgroundSize: '100% 100%'
    },
    box: {
        position: 'absolute',
        top: 0,
        right: 0,
        bottom: 0,
        left: 0,
        height: 495,
        width: 800,
        margin: 'auto',
        // backgroundColor: '#fff'
        overflow: 'hidden',
    },
    left: {
        float: 'left',
        width: 350,
        height: '100%',
        backgroundColor: '#fff',
        // backgroundColor: '#28417f',
        padding: '100px 20px',
        // opacity: 0.9,
    },
    right: {
        float: 'left',
        width: 450,
        height: '100%',
        background: '#ffffff url(' + ad + ') no-repeat center center',
        backgroundSize: 'cover'
        // padding:20
    },
    title: {
        fontWeight: 'bolder',
        color: '#00032c',
        textAlign: 'center',
        fontSize: 20,
        margin: '20px 0'
    }
}


export class Login extends Component {

    constructor(props) {
        super(props);
        this.basic_url = globalConfig.domain + '/api/common/img_verification_code'
        this.state = {
            verification_img_url: this.basic_url
        }
    }

    onFinish = (v) => {
        axios.post(
            globalConfig.domain + '/api/user/login', v
        ).then(
            r => {
                // console.log(r)
                message.success('login success');
                window.sessionStorage.setItem('user_info', JSON.stringify(r.data.data))
                window.location.reload();
            }
        ).catch(
            e => {
                message.error(e.response.data.detail);
                // window.location.reload();
                let dateTime = Date.now();
                let timestamp = Math.floor(dateTime / 1000);
                this.setState({
                    verification_img_url: this.basic_url + '?ts=' + timestamp
                })
            }
        )
    }

    render() {
        return (
            <div style={styles.bg}>
                <div style={styles.box}>
                    <div style={styles.left}>
                        <p style={styles.title}>User Login</p>
                        <Form
                            layout={'vertical'}
                            onFinish={this.onFinish}
                        >
                            <Form.Item
                                // label="Username"
                                name="username"
                                rules={[
                                    {
                                        required: true,
                                        message: 'please input username',
                                        max: 20,
                                    }
                                ]}
                            >
                                <Input prefix={<UserOutlined/>} placeholder={'Username'}/>
                            </Form.Item>

                            <Form.Item
                                name='password'
                                // label='Password'
                                rules={[
                                    {
                                        required: true,
                                        message: 'please input username',
                                        max: 30,
                                    }
                                ]}
                            >
                                <Input prefix={<LockOutlined/>} type={'password'} placeholder={'Password'}/>
                            </Form.Item>

                            <Form.Item
                                // label={'verification code'}
                                name={'verify_code'}
                                rules={[
                                    {
                                        required: true,
                                        message: 'please input verification code',
                                        max: 5,
                                    }
                                ]}
                            >
                                <Row gutter={8}>
                                    <Col span={15}>
                                        <Input style={{marginTop: 2}} prefix={<VerifiedOutlined/>}
                                               placeholder={'Verification'}/>
                                    </Col>
                                    <Col>
                                        <img
                                            alt={'verification code'} width={110}
                                            src={this.state.verification_img_url}
                                            onClick={e => {
                                                let dateTime = Date.now();
                                                let timestamp = Math.floor(dateTime / 1000);
                                                e.target.src = this.basic_url + '?ts=' + timestamp
                                            }}
                                        />
                                    </Col>
                                </Row>

                            </Form.Item>

                            <Button type="primary" htmlType="submit">
                                Login
                            </Button>
                        </Form>
                    </div>

                    <div style={styles.right}>
                    </div>
                </div>
            </div>
        )
    }
}