import React, {Component} from "react";
import {Badge, Button, Card, Table, Space, Modal, Form, Input, Select, Radio, message} from "antd";
import {EditOutlined, DeleteOutlined, PlusOutlined} from "@ant-design/icons";
import {globalConfig} from "../../common";
import axios from "axios";


export class Vip extends Component {
    constructor(props) {
        super(props);
        this.formRef = React.createRef()
        this.start_ip = '';
        this.end_ip = '';
        this.editData = {};

        this.state = {
            dateSource: [],
            visible: false,
        }
    }

    isp2cn = (v) => {
        let result = ''
        // eslint-disable-next-line default-case
        switch (v) {
            case 'china-mobile':
                result = '';
                break;

            case 'telecom':
                result = '';
                break;

            case 'unicom':
                result = '';
                break;

            case 'private':
                result = '';
                break;

            case 'other':
                result = '';
                break;
        }
        return result
    }

    handleDeleteVip = (id) => {
        return Modal.confirm({
            title: 'delete vip pool',
            okType: "danger",
            onOk: () => {
                axios.delete(
                    globalConfig.domain + '/api/config/vip/setVip', {
                        params: {
                            id: id
                        }
                    }
                ).then(
                    r => {
                        message.success(r.data.msg);
                        this.getAllRecord();
                    }
                )
            }
        })
    }

    columns = [
        {title: 'Name', dataIndex: 'name',},
        {title: 'Address Range', dataIndex: 'ip_range', render: v => v.replaceAll('-', ' - ')},
        {title: 'ISP', dataIndex: 'isp', render: v => this.isp2cn(v)},
        {
            title: 'State',
            dataIndex: 'state',
            render: (v) => v.toLowerCase() === 'e' ? <Badge status="success" text={'enable'}/> :
                <Badge status="error" text={'disable'}/>
        },

        {title: 'Pub Date', dataIndex: 'pub_date', render: v => v.substr(0, 16)},
        {
            title: 'Action', dataIndex: 'id', render: (v, record) => {
                return (
                    <Space>
                        <Button icon={<EditOutlined/>} onClick={
                            () => {
                                let x = record.ip_range.split('-')
                                this.start_ip = x[0]
                                this.end_ip = x[1]
                                this.editData = record;
                                this.setState({visible: true})
                            }
                        }>Edit</Button>
                        <Button icon={<DeleteOutlined/>} onClick={() => this.handleDeleteVip(v)}>Delete</Button>
                    </Space>
                )
            }
        },
    ];

    getAllRecord = () => {
        axios.get(
            globalConfig.domain + '/api/config/vip/setVip'
        ).then(
            r => this.setState({dataSource: r.data.data})
        )
    }

    componentDidMount() {
        this.getAllRecord();
    }


    onFinish = (v) => {
        let url = globalConfig.domain + '/api/config/vip/setVip'
        let req;
        if (this.editData.id !== undefined && this.editData.id > 0) {
            url += '?id=' + this.editData.id
            req = axios.put(
                url, v

            )
        } else {
            req = axios.post(
                url, v
            )
        }

        req.then(
            r => {
                message.success(r.data.msg);
                this.getAllRecord();
                this.setState({visible: false})
            }
        ).catch(
            e => message.error(e.response.data.detail)
        )
    }

    render() {
        return (
            <Card title={'Virtual Ip Pool'}>
                <Space direction={'vertical'} size={'middle'} style={{width: '100%'}}>
                    <Button type={'primary'} icon={<PlusOutlined/>} onClick={() => this.setState({visible: true})}>Add
                        Ip Pool</Button>
                    <Table
                        pagination={false}
                        dataSource={this.state.dataSource}
                        columns={this.columns}
                        rowKey={(record) => record.id}
                    />
                </Space>
                <Modal
                    destroyOnClose={true}
                    title={"add virtual ip pool"}
                    visible={this.state.visible}
                    onOk={() => this.formRef.current.submit()}
                    onCancel={
                        () => {
                            this.start_ip = '';
                            this.end_ip = '';
                            this.setState({visible: false});
                            this.editData = {};
                        }
                    }
                >
                    <Form
                        ref={this.formRef}
                        onFinish={this.onFinish}
                        layout={'vertical'}
                        initialValues={this.editData}
                    >
                        <Form.Item
                            name={'name'}
                            label={'Name'}
                            rules={[
                                {
                                    required: true,
                                    max: 20,
                                    message: 'please input a name'
                                }
                            ]}
                        >
                            <Input placeholder={'pool name'} max={20}/>
                        </Form.Item>

                        <Form.Item
                            name={'ip_range'}
                            label={'IP Range'}
                            rules={[
                                {
                                    required: true,
                                    message: 'please input ip range',
                                    max: 31,
                                }
                            ]}
                        >
                            <Input.Group compact>
                                <Input
                                    style={{textAlign: 'center', width: '45%'}} max={15}
                                    placeholder="192.168.100.1"
                                    defaultValue={this.start_ip}
                                    onChange={
                                        v => {
                                            this.start_ip = v.target.value;
                                            this.formRef.current.setFieldsValue({
                                                ip_range: this.start_ip + '-' + this.end_ip
                                            })
                                        }
                                    }
                                />
                                <Input
                                    // className="site-input-split"
                                    style={{
                                        width: '10%',
                                        textAlign: 'center',
                                        borderLeft: 0,
                                        borderRight: 0,
                                        pointerEvents: 'none',
                                    }}
                                    placeholder="-"
                                    disabled
                                />
                                <Input
                                    max={15}
                                    className="site-input-right"
                                    style={{
                                        width: '45%',
                                        textAlign: 'center',
                                    }}
                                    defaultValue={this.end_ip}
                                    placeholder="192.168.100.10"
                                    onChange={
                                        v => {
                                            this.end_ip = v.target.value;
                                            this.formRef.current.setFieldsValue({
                                                ip_range: this.start_ip + '-' + this.end_ip
                                            })
                                        }
                                    }
                                />
                            </Input.Group>
                        </Form.Item>

                        <Form.Item
                            name={'isp'}
                            label={'Isp'}
                            rules={[{
                                required: true,
                                message: 'please choice a isp'
                            }]}
                        >
                            <Select>
                                <Select.Option value="telecom"></Select.Option>
                                <Select.Option value="unicom"></Select.Option>
                                <Select.Option value="china-mobile"></Select.Option>
                                <Select.Option value="private"></Select.Option>
                                <Select.Option value="other"></Select.Option>
                            </Select>
                        </Form.Item>

                        <Form.Item
                            name={'state'}
                            label={'State'}
                            rules={[{
                                required: true,
                                message: 'please choice a state'
                            }]}
                        >
                            <Radio.Group buttonStyle="solid" style={{width: '100%'}}>
                                <Radio.Button value="e"
                                              style={{width: '50%', textAlign: 'center'}}>Enable</Radio.Button>
                                <Radio.Button value="d"
                                              style={{width: '50%', textAlign: 'center'}}>Disable</Radio.Button>
                            </Radio.Group>
                        </Form.Item>
                    </Form>
                </Modal>
            </Card>
        )
    }
}