import React, {Component} from "react";
import {Breadcrumb, Layout, Menu} from "antd";
import {ExceptionOutlined, ReloadOutlined } from "@ant-design/icons";

import {Sessions} from "./component/sessions";
import {RunningConfig} from "./component/runningConfig";

const {Sider, Content} = Layout

export class Report extends Component {

    constructor(props) {
        super(props);
        this.state = {
            content: <Sessions />
        }
    }

    handleMenuClick = (e) => {
        //console.log(e.key)
        //eslint-disable-next-line default-case
        switch (e.key) {
            case 'sessions':
                this.setState({content: <Sessions/>})
                break;
            case 'run_config':
                this.setState({content: <RunningConfig/>})
                break;
        }
    }

    render() {
        return (
            <React.Fragment>
                <Breadcrumb style={{margin: '16px 0'}}>
                    <Breadcrumb.Item>Home</Breadcrumb.Item>
                    <Breadcrumb.Item>Report</Breadcrumb.Item>
                </Breadcrumb>
                <Layout className="site-layout-background" style={{padding: '24px 0'}}>
                    <Sider className="site-layout-background" width={200}>
                        <Menu
                            mode="inline"
                            defaultSelectedKeys={['event']}
                            // defaultOpenKeys={['sub1']}
                            style={{height: '100%'}}
                            onClick={this.handleMenuClick}
                        >

                            <Menu.Item key="sessions" icon={<ExceptionOutlined />}>Sessions</Menu.Item>
                            <Menu.Item key="run_config" icon={<ReloadOutlined />}>Configuration</Menu.Item>
                            {/*<SubMenu key="sub2" icon={<LaptopOutlined/>} title="Route">*/}
                            {/*    <Menu.Item key="s_route">static route</Menu.Item>*/}
                            {/*    <Menu.Item key="6">policy route</Menu.Item>*/}
                            {/*</SubMenu>*/}
                        </Menu>
                    </Sider>
                    <Content style={{padding: '0 8px', minHeight: 280}}>{this.state.content}</Content>
                </Layout>
            </React.Fragment>
        )
    }
}