import React, {Component} from "react";
import {
    Badge,
    Button,
    Card,
    Form,
    Input,
    message,
    Modal,
    Space,
    Spin,
    Table,
    Alert
} from "antd";
import axios from "axios";
import {globalConfig} from "../../common";
import {
    DownloadOutlined,
    DeleteOutlined,
    PlusOutlined,
    ControlOutlined,
    SafetyCertificateOutlined
} from "@ant-design/icons";

let styles = {
    itemField: {
        display: 'inline-block',
        fontWeight: 'bolder',
        width: 150,
        paddingRight: 20,
        textAlign: 'left',
        height: 28,
    },
    spin: {
        position: 'absolute',
        left: 0,
        top: '50%',
        right: 0,
        bottom: '50%',
    }
}

export class OpenVpnStatus extends Component {
    constructor(props) {
        super(props);
        this.formRef = React.createRef();
        this.state = {
            status: '',
            server: '',
            dns: '8.8.4.4',
            listen_port: 1194,
            listen_ip: '10.10.0.1',
            loading: false,
            modal_spin: false,
            visible: false,
        }
    }

    get_openvpn_server_status = () => {
        axios.get(
            globalConfig.domain + '/api/apps/openvpn/getOpenVpnStatus'
        ).then(
            r => {
                this.setState(r.data.data);
            }
        )
    }

    start_or_stop_server = (cmd) => {
        if (cmd !== 'start' && cmd !== 'stop') {
            message.error('error command')
            return
        }
        let post_data = this.state;
        post_data.status = cmd;
        for (let i in post_data) {
            if (post_data[i] === undefined || post_data[i] === null || post_data[i].length === 0) {
                message.error(i + ' field is empty!')
                return;
            }
        }
        // console.log(post_data)
        this.setState({loading: true});
        axios.post(
            globalConfig.domain + '/api/apps/openvpn/startOrStopServer',
            post_data
        ).then(
            r => {
                message.success(r.data.msg);
                this.setState({loading: false});
                this.get_openvpn_server_status();
            }
        ).catch(
            e => message.error(e.response.data.detail)
        )

    }

    onFinish=(values)=>{
        this.setState({modal_spin: true})
        axios.post(
            globalConfig.domain+'/api/apps/openvpn/genCert', values
        ).then(
            r=>{
                message.success(r.data.msg);
                this.setState({visible: false, modal_spin: false})
            }
        ).catch(
            e=>{
                message.error(e.response.data.detail);
                this.setState({modal_spin: false})
            }
        )
    }

    componentDidMount() {
        this.get_openvpn_server_status();
    }

    render() {
        return <Card title={'OpenVPN server status'}>
            <Spin spinning={this.state.loading}>
                <p>
                    <b style={styles.itemField}>Status</b>
                    <Badge status={this.state.status === 'running' ? 'success' : 'error'} text={this.state.status}/>
                    <Button type={'link'} onClick={
                        () => this.start_or_stop_server(this.state.status === 'running' ? 'stop' : 'start')
                    }>{this.state.status === 'running' ? 'stop' : 'start'}</Button>
                    <Button
                        type={'primary'}
                        shape="circle"
                        style={{float: 'right'}}
                        disabled={this.state.status === 'running'}
                        onClick={()=>this.setState({visible: true})}
                        icon={<SafetyCertificateOutlined />}
                    />
                </p>
                {
                    this.state.status === 'running' ? (
                        <div>
                            <p><b style={styles.itemField}>Protocol</b>udp</p>
                            <p><b style={styles.itemField}>Listen IP</b>{this.state.listen_ip}</p>
                            <p><b style={styles.itemField}>Listen Port</b>{this.state.listen_port}</p>
                            <p><b style={styles.itemField}>Server Network</b>{this.state.server}</p>
                            <p><b style={styles.itemField}>Dns Address</b>{this.state.dns}</p>
                        </div>
                    ) : (
                        <div>
                            <p>
                                <b style={styles.itemField}>Listen IP</b>
                                <Input
                                    style={{width: 200}}
                                    value={this.state.listen_ip}
                                    onChange={v => this.setState({listen_ip: v.target.value})}
                                />
                            </p>

                            <p>
                                <b style={styles.itemField}>Listen Port</b>
                                <Input
                                    style={{width: 200}}
                                    value={this.state.listen_port}
                                      onChange={v => this.setState({listen_port: v.target.value * 1.0})}
                                />
                            </p>

                            <p>
                                <b style={styles.itemField}>Server Subnet</b>
                                <Input
                                    style={{width: 400}}
                                    value={this.state.server}
                                    placeholder={'10.88.0.0/24'}
                                    onChange={v => this.setState({server: v.target.value})}
                                />
                            </p>

                            <p>
                                <b style={styles.itemField}>Dns Address</b>
                                <Input
                                    style={{width: 400}}
                                    value={this.state.dns}
                                    placeholder={'8.8.8.8'}
                                    onChange={v => this.setState({dns: v.target.value})}
                                />
                            </p>
                        </div>
                    )
                }
            </Spin>
            <Modal
                destroyOnClose={true}
                title={"generate certification"}
                visible={this.state.visible}
                onOk={() => this.formRef.current.submit()}
                onCancel={() => this.setState({visible: false, modal_spin: false})}
            >
                <Spin spinning={this.state.modal_spin}>
                <Form
                    ref={this.formRef}
                    onFinish={this.onFinish}
                    layout={'vertical'}
                >
                    <Form.Item
                        name={'country'}
                        label={'Country'}
                        rules={[
                            {
                                required: true,
                                max: 2,
                                message: 'please input country'
                            }
                        ]}
                    >
                        <Input placeholder={'CN'} max={2}/>
                    </Form.Item>

                    <Form.Item
                        name={'province'}
                        label={'Province'}
                        rules={[
                            {
                                required: true,
                                max: 4,
                                message: 'please input province'
                            }
                        ]}
                    >
                        <Input placeholder={'HN'} max={4}/>
                    </Form.Item>

                    <Form.Item
                        name={'city'}
                        label={'City'}
                        rules={[
                            {
                                required: true,
                                max: 10,
                                message: 'please input city name'
                            }
                        ]}
                    >
                        <Input placeholder={'YiYang'} max={10}/>
                    </Form.Item>

                    <Form.Item
                        name={'org'}
                        label={'Organization'}
                        rules={[
                            {
                                required: true,
                                max: 20,
                                message: 'please input Organization name'
                            }
                        ]}
                    >
                        <Input placeholder={'FuRong Cloud'} max={20}/>
                    </Form.Item>

                    <Form.Item
                        name={'org_unit'}
                        label={'Organization Unit'}
                        rules={[
                            {
                                required: true,
                                max: 20,
                                message: 'please input Organization unit'
                            }
                        ]}
                    >
                        <Input placeholder={'It'} max={20}/>
                    </Form.Item>
                    <Form.Item
                        name={'email'}
                        label={'E-mail'}
                        rules={[
                            {
                                type: 'email',
                                required: true,
                                max: 50,
                                message: 'please input email'
                            }
                        ]}
                    >
                        <Input placeholder={'a@b.com'} max={50}/>
                    </Form.Item>
                    <Alert type={'warning'} message={'Generate Certification will delete all exists user!!!'}/>
                </Form>
                </Spin>
            </Modal>
        </Card>
    }
}


export class OpenVpnUsers extends Component {
    constructor(props) {
        super(props);
        this.formRef = React.createRef()
        this.userInfoFormRef = React.createRef()
        this.state = {
            dataSource: [],
            pageInfo: {
                current: 1,
                pageSize: 10,
                total: 0
            },
            visible: false,
            loading: false,

            change_user_info_box: false,
            user_ip: null,
            user_route: null,
            user_id: 0,
        }
    }

    openvpn_users_columns = [
        {title: "Username", dataIndex: "username"},
        {title: "Name", dataIndex: "name"},
        {title: "User Ip", dataIndex: "user_ip"},
        {title: "Create Time", dataIndex: "join_date"},
        // {
        //     title: "State",
        //     dataIndex: "state",
        //     render: (v => <Badge status={v ? 'success' : 'error'} text={v ? 'active' : 'inactive'}/>)
        // },
        {
            title: "Operate", dataIndex: "id", render: (v, record) => {
                return (
                    <Space>
                        <Button icon={<DownloadOutlined/>}
                                onClick={() => window.open(globalConfig.domain + '/api/apps/openvpn/ovpnDownload/' + v + '/')}>ovpn</Button>
                        <Button icon={<DeleteOutlined/>} onClick={() => this.delete_user(v)}>delete</Button>
                        <Button icon={<ControlOutlined/>} onClick={
                            ()=>this.setState({
                                user_ip: record.user_ip,
                                user_route: record.user_route,
                                change_user_info_box: true,
                                user_id: record.id,
                            })
                        }>Modify</Button>
                    </Space>
                )
            }
        }
    ]

    modify_user_info=(values)=>{
        values.uid=this.state.user_id;
        axios.put(
            globalConfig.domain+'/api/apps/openvpn/openVpnUserSet',values
        ).then(
            r=>{
                message.success('success');
                this.get_openvpn_users_list(this.state.pageInfo.current);
                this.setState({change_user_info_box: false})
            }
        ).catch(
            e=>message.error(e.response.data.detail)
        )
    }

    get_openvpn_users_list = (page = 1) => {
        axios.get(
            globalConfig.domain + '/api/apps/openvpn/openVpnUserSet', {
                params: {
                    page: page
                }
            }
        ).then(
            r => {
                this.setState({dataSource: r.data.data});
            }
        ).catch(e => message.error(e.response.data.detail))
    }

    delete_user = (uid) => {
        this.setState({loading: true});
        Modal.confirm({
            title: 'warnning',
            content: 'Are you sure you want to delete this user',
            onCancel: ()=>this.setState({loading: false}),
            onOk: () => {
                axios.delete(
                    globalConfig.domain + '/api/apps/openvpn/openVpnUserSet', {
                        params: {
                            uid: uid
                        }
                    }
                ).then(
                    r => {
                        message.success(r.data.msg);
                        this.setState({loading: false});
                        this.get_openvpn_users_list(this.state.pageInfo.current);
                    }
                )
            }
        })

    }

    onFinish = (values) => {
        this.setState({loading: true});
        axios.post(
            globalConfig.domain + '/api/apps/openvpn/openVpnUserSet',
            values
        ).then(
            r => {
                message.success(r.data.msg);
                this.setState({visible: false, loading: false})
                this.get_openvpn_users_list(this.state.pageInfo.current);
            }
        ).catch(e => message.error(e.response.data.detail))
    }

    componentDidMount() {
        this.get_openvpn_users_list();
    }

    render() {
        return <React.Fragment>
            <Card title={"OpenVPN Users"}>
                <Button type={'primary'} style={{marginBottom: 15}} icon={<PlusOutlined/>}
                        onClick={() => this.setState({visible: true})}>Add Openvpn User</Button>
                <Spin spinning={this.state.loading}>
                    <Table
                        columns={this.openvpn_users_columns}
                        dataSource={this.state.dataSource}
                        rowKey={'id'}
                        pagination={
                            {
                                current: this.state.pageInfo.current,
                                total: this.state.pageInfo.total,
                                pageSize: this.state.pageInfo.pageSize,
                                onChange: (page, pageSize) => this.get_openvpn_users_list(page)
                            }
                        }
                        expandable={
                            {
                                expandedRowRender: record => <div
                                    style={{margin: 0}}
                                    dangerouslySetInnerHTML={
                                        {__html: '<b>Client pushed routes</b></br>' + record.user_route.replaceAll(/\n/g, '</br>')}
                                    }/>,
                                // rowExpandable: record => record.name !== 'Not Expandable',
                            }
                        }
                    />
                </Spin>

            </Card>

            <Modal
                destroyOnClose={true}
                title={"add openvpn user"}
                visible={this.state.visible}
                onOk={() => this.formRef.current.submit()}
                onCancel={() => this.setState({visible: false})}
            >
                <Form
                    ref={this.formRef}
                    onFinish={this.onFinish}
                    layout={'vertical'}
                >
                    <Form.Item
                        name={'name'}
                        label={'Name'}
                        rules={[
                            {
                                required: true,
                                max: 6,
                                message: 'please input a name'
                            }
                        ]}
                    >
                        <Input placeholder={'Alice'} max={6}/>
                    </Form.Item>

                    <Form.Item
                        name={'username'}
                        label={'Username'}
                        rules={[
                            {
                                required: true,
                                max: 20,
                                message: 'please input a username'
                            }
                        ]}
                    >
                        <Input placeholder={'username'} max={20}/>
                    </Form.Item>

                    <Form.Item
                        name={'user_ip'}
                        label={'User Static Ip Address'}
                        rules={[
                            {
                                required: true,
                                max: 20,
                                message: 'please input a private ip address'
                            }
                        ]}
                    >
                        <Input placeholder={'10.0.88.1'} max={15}/>
                    </Form.Item>

                    <Form.Item
                        name={'user_route'}
                        label={'Push routes '}
                        rules={[
                            {
                                required: true,
                                max: 500,
                                message: 'please input route record'
                            }
                        ]}
                    >
                        <Input.TextArea placeholder={'192.168.100.0 255.255.255.0'}/>
                    </Form.Item>
                </Form>
            </Modal>

            <Modal
                destroyOnClose={true}
                title={"Modify user ip route"}
                visible={this.state.change_user_info_box}
                onOk={() => this.userInfoFormRef.current.submit()}
                onCancel={() => this.setState({change_user_info_box: false})}
            >
                <Form
                    ref={this.userInfoFormRef}
                    onFinish={this.modify_user_info}
                    layout={'vertical'}
                    initialValues={
                        {
                            user_ip: this.state.user_ip,
                            user_route: this.state.user_route,
                        }
                    }
                >
                    <Form.Item
                        name={'user_ip'}
                        label={'User Static Ip Address'}
                        rules={[
                            {
                                required: true,
                                max: 20,
                                message: 'please input a private ip address'
                            }
                        ]}
                    >
                        <Input placeholder={'10.0.88.1'} max={15}/>
                    </Form.Item>

                    <Form.Item
                        name={'user_route'}
                        label={'Push routes '}
                        rules={[
                            {
                                required: true,
                                max: 500,
                                message: 'please input route record'
                            }
                        ]}
                    >
                        <Input.TextArea placeholder={'192.168.100.0 255.255.255.0'}/>
                    </Form.Item>
                </Form>
            </Modal>
        </React.Fragment>
    }
}
